import React from 'react'
import ImageGallery from 'react-image-gallery'
import '../../../node_modules/react-image-gallery/styles/css/image-gallery-no-icon.css'
import styled from 'styled-components'

class Carousel extends React.Component {

    render() {
        const images = [
            {
                original: 'images/Event1.jpg',
                description: 'Teste'
            },
            {
                original: 'images/Event2.jpg',
                description: 'Teste'
            },
            {
                original: 'images/Event3.jpg',
                description: 'Teste'
            },
        ];
        const ImageWrapper = styled.div`
            margin-bottom: 20px;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
        `

        return (
            <ImageWrapper>
                <ImageGallery items={images} autoPlay={true} showThumbnails={false} />
            </ImageWrapper>
        );
    }
}

export { Carousel }
