import React from 'react'
import { NewEventsWrapper, NewEventsItem, NewEventsItemImage, NewEventsItemName, NewEventsButton, PlusIcon } from './NewEvents.style'
import { Row, Column } from 'styles'
import { history } from 'helpers'

class NewEvents extends React.Component {

    handleMoreEvent = () => {
        history.push('/eventos');
    }

    render() {

        return (
            <NewEventsWrapper>
                <Row bottom="10px">
                    <Column>
                        <NewEventsItem>
                            <NewEventsItemImage imageSrc="images/Event1.jpg" />
                            <NewEventsItemName>Events</NewEventsItemName>
                        </NewEventsItem>
                    </Column>
                    <Column>
                        <NewEventsItem>
                            <NewEventsItemImage imageSrc="images/Event2.jpg" />
                            <NewEventsItemName>Events</NewEventsItemName>
                        </NewEventsItem>
                    </Column>
                </Row>
                <Row>
                    <Column>
                        <NewEventsItem>
                            <NewEventsItemImage imageSrc="images/Event3.jpg" />
                            <NewEventsItemName>Events</NewEventsItemName>
                        </NewEventsItem>
                    </Column>
                    <Column>
                        <NewEventsButton onClick={this.handleMoreEvent}><PlusIcon size={50} /></NewEventsButton>
                    </Column>
                </Row>
            </NewEventsWrapper>
        );
    }
}

export { NewEvents }
