import styled from 'styled-components'
import { Plus } from 'styled-icons/evil'
import { colors } from 'styles'

export const NewEventsWrapper = styled.div`
    padding: 0 10px 10px;
`;

export const NewEventsItem = styled.div`
    background-color: ${colors.orange};
    position: relative;
    height: 150px;
    box-shadow: 0 0 4px 1px rgba(0, 0, 0, 0.3);
`;

export const NewEventsItemImage = styled.img`
    ${({ imageSrc }) => `background-image: url("${imageSrc}")`}; 
    height: 150px;
    width: 100%;
    background-color: ${colors.white};
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

export const NewEventsItemName = styled.p`
    background-color: rgba(0, 0, 0, 0.5);
    left: 0;
    margin:0
    padding: 2px 5px;
    position: absolute;
    bottom: 20px;
    height: 20px;
    color: ${colors.white};
`;

export const NewEventsButton = styled.button`
    height: 100%;
    width: 100%;
    background-color: ${colors.orange};
    border: 2px solid ${colors.orange};
`;

export const PlusIcon = styled(Plus)`
    color: ${colors.lightGray3};
`;