import React from 'react'
import { EventItemWrapper, EventItemImage } from './EventListItem.style'
import { Row, Column, Text, colors } from 'styles'
import { history } from 'helpers'

class EventListItem extends React.Component {

    openEventItem = () => {
        history.push('/')
    }

    render() {


        return (
            <EventItemWrapper onClick={this.openEventItem}>
                <Row>
                    <Column flex={0.3}>
                        <EventItemImage imageSrc="images/Event1.jpg" />
                    </Column>
                    <Column flex={0.7}>
                        <Text color={colors.red}>Data Evento</Text>
                        <Text size={13} bold={600}>Nome Evento</Text>
                        <Text color={colors.lightGray} size={14}>Endere√ßo Evento</Text>
                    </Column>
                </Row>
            </EventItemWrapper>
        );
    }
}

export { EventListItem }
